@function rel-val($val, $fz, $round: 1000) 
    @return #{calc(round(calc($val / $fz * $round)) / $round)}em

@function calcFluidFontSize($fs, $w-b, $ind)
    @return calc((100vw - #{$ind}px) * ($fs  / ($w-b - $ind)))

@mixin fluidFontSize($fs, $w-min, $w-max, $ind: 0)
    @media (min-width: #{$w-min}px) and (max-width: #{$w-max - 1}px)
        font-size: calcFluidFontSize($fs, $w-max, $ind)