.involvement {
    overflow: hidden;
    @include fluidFontSize(16, 0, 1200);
    background: #eef2f5;
    height: 800vh;

    &__wrapper {
        position: relative;
        width: 100%;
        top: 0;
    }

    &__symbol {
        pointer-events: none;
        position: absolute;
        top: 50vh;
        left: 35.8125vw;
        opacity: 0.06;
        width: rel-val(538, 16);
        transform: translate(-50%, 0);
        transform-origin: 0 50%;
        color: $color-primary;
        .icon {
            width: 100%;
        }
    }
}
