* {
  box-sizing: border-box;
}

body {
  font-size: 16px;
  display: flex;
  flex-direction: column;
  background: #FFF;
  font-family: "Oswald-Regular", Arial, sans-serif;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body.opened {
  overflow: hidden;
}

li {
  list-style-type: none;
}

ul {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

h1, h2, h3, h4, p {
  margin: 0;
  font-weight: normal;
}

iframe, img, svg, video {
  vertical-align: top;
  height: auto;
}

.container {
  width: 100%;
  min-width: 290px;
  margin: 0 auto;
  padding: 0 15px;
}

.header {
  z-index: 10;
  color: #fff;
  height: 92px;
  font-size: 18px;
  line-height: 1.333;
  position: fixed;
  width: 100%;
}
.header--shifted, .opened .header {
  transition: background 0.3s;
  background: #fff;
  color: #0C2742;
  box-shadow: 0 0 0 #000;
}
.opened .header {
  height: 100vh;
  font-size: 25px;
}
.header__container {
  height: 100%;
  max-width: 1350px;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  flex-wrap: wrap;
}
.header--shifted .header__container, .opened .header__container {
  align-items: center;
}
.header__logos {
  width: 174px;
  transition: transform 0.3s;
  transform-origin: 0 50%;
}
.header--shifted .header__logos, .opened .header__logos {
  transform: scale(0.9);
}
.header__logo-primary {
  display: block;
}
.header--shifted .header__logo-primary, .opened .header__logo-primary {
  display: none;
}
.header__logo-default {
  display: none;
}
.header--shifted .header__logo-default, .opened .header__logo-default {
  display: block;
}
.header__content {
  overflow: auto;
  display: flex;
  height: 100%;
  align-items: inherit;
}
.opened .header__content {
  order: 1;
  display: block;
  width: 100%;
}
@media (max-width: 899px) {
  .header__content {
    display: none;
  }
}
.header__nav {
  height: 100%;
}
.opened .header__nav {
  height: auto;
}
.header__nav-list {
  height: 100%;
  display: flex;
}
.opened .header__nav-list {
  align-items: center;
  flex-direction: column;
  margin-bottom: 25px;
}
.header__nav-item {
  height: 100%;
}
.header__nav-link {
  border-radius: 0 0 4px 4px;
  height: 100%;
  display: flex;
  align-items: flex-end;
  padding: 0 17px 19px 17px;
  transition-duration: 0.3s;
  transition-property: background, color;
}
.header--shifted .header__nav-link, .opened .header__nav-link {
  align-items: center;
  padding-bottom: 0;
  border-radius: 0;
}
.opened .header__nav-link {
  padding: 16px 25px;
}
.header__nav-link:hover {
  background: #1B75BC;
  color: #fff;
}
.header__nav-link--selected {
  background: #1B75BC;
  color: #fff;
}
.header__btn {
  display: flex;
  align-items: center;
  padding: 14px 22.5px;
  background: #fff;
  border-radius: 4px;
  color: #1B75BC;
  margin: 0 0 3px 16px;
  transition-duration: 0.3s;
  transition-property: background, border-color, color;
  border-style: solid;
  border-width: 2px;
  border-color: #fff;
}
.header__btn:hover {
  border-color: #1B75BC;
  background: #1B75BC;
  color: #fff;
}
.header--shifted .header__btn, .opened .header__btn {
  border-color: #1B75BC;
  background: #1B75BC;
  color: #fff;
}
.header--shifted .header__btn:hover, .opened .header__btn:hover {
  background: #fff;
  color: #1B75BC;
}
.opened .header__btn {
  transform: translateX(-50%);
  margin: 0 0 0 50%;
  display: inline-block;
}
.header__burger {
  cursor: pointer;
  align-self: center;
  background: none;
  border: none;
  position: relative;
  width: 40px;
  min-width: 25px;
  height: 40px;
  margin: 26px 0;
}
@media (min-width: 900px) {
  .header__burger {
    display: none;
  }
  .opened .header__burger {
    display: block;
  }
}
.header__burger-line {
  position: absolute;
  height: 4px;
  width: 100%;
  background: #fff;
  display: block;
  left: 0;
  transition: 0.3s ease-in-out;
}
.header__burger-line:nth-child(1) {
  top: 5px;
}
.header__burger-line:nth-child(2) {
  top: calc(50% - 2px);
}
.header__burger-line:nth-child(3) {
  top: calc(100% - 9px);
}
.opened .header__burger-line:nth-child(1) {
  top: calc(50% - 2px);
  transform: rotate(-45deg);
}
.opened .header__burger-line:nth-child(2) {
  width: 0%;
  left: 50%;
  transform: rotate(360deg);
}
.opened .header__burger-line:nth-child(3) {
  top: calc(50% - 2px);
  transform: rotate(-135deg);
}
.header--shifted .header__burger-line, .opened .header__burger-line {
  background: #0C2742;
}

.main {
  flex-grow: 1;
  overflow: hidden;
}

.primary {
  position: relative;
  overflow: hidden;
  padding-top: 92px;
}
@media (min-width: 0px) and (max-width: 599px) {
  .primary {
    font-size: calc((100vw - 30px) * 0.0280701754);
  }
}
.primary__container {
  height: 50.5em;
  padding-top: 9.813em;
  max-width: 1278px;
}
.primary__wrapper {
  max-width: 1221px;
  margin-left: auto;
}
.primary__background {
  z-index: -1;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: #1B75BC;
}
.primary__background::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: linear-gradient(258.33deg, rgba(12, 39, 66, 0.2) 0%, rgba(12, 39, 66, 0.8) 100%);
}
.primary__background img, .primary__background-video {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.primary__title {
  color: #fff;
  line-height: 1.05;
  display: flex;
  flex-direction: column;
  text-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  letter-spacing: 0.003em;
  margin-bottom: 0.775em;
  font-size: 7.5em;
}
.primary__title-short {
  margin-bottom: 0.1em;
  opacity: 0;
  animation: primary-opacity 1s linear 0s forwards;
}
.primary__title-anim {
  margin-bottom: -0.1em;
  height: 1.05em;
  overflow: hidden;
  color: #f9ee8e;
}
.primary__title-anim-inner {
  display: flex;
  flex-direction: column;
  opacity: 0;
  animation: primary-title-anim 9s infinite 0.5s;
}
.primary__bottom {
  display: flex;
  align-items: flex-end;
  flex-wrap: wrap;
  justify-content: space-between;
  color: rgba(255, 255, 255, 0.8);
}
.primary__text {
  font-family: "Poppins-Regular", Arial, sans-serif;
  line-height: 1.4;
  max-width: 570px;
  opacity: 0;
  white-space: nowrap;
  animation: primary-opacity 1s linear 0.5s forwards;
  letter-spacing: 0.014em;
  margin-right: 2.321em;
  margin-bottom: 0.714em;
  font-size: 1.75em;
}
.primary__pointer {
  display: flex;
  align-items: center;
  margin-bottom: 1.25em;
  opacity: 0;
  transform: rotate(90deg);
  animation: primary-opacity 1s linear 0s forwards, primary-pointer-rotate 0.5s linear 0s forwards;
}
.primary__pointer .icon {
  transform: rotate(135deg);
  margin-right: 12px;
