.primary {
    @include fluidFontSize(16, 0, 600, 30);
    position: relative;
    overflow: hidden;
    padding-top: 92px;

    &__container {
        height: rel-val(808, 16);
        padding-top: rel-val(157, 16);
        max-width: 1278px;
    }

    &__wrapper {
        max-width: 1221px;
        margin-left: auto;
    }

    &__background {
        z-index: -1;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background: $color-primary;
        &::after {
            content: "";
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            background: linear-gradient(258.33deg, rgba(12, 39, 66, 0.2) 0%, rgba(12, 39, 66, 0.8) 100%);
        }
    }

    &__background img,
    &__background-video {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        height: 100%;
        width: 100%;
        object-fit: cover;
    }

    &__title {
        color: #fff;
        line-height: 1.05;
        display: flex;
        flex-direction: column;
        text-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
        letter-spacing: rel-val(0.4, 120);
        margin-bottom: rel-val(93, 120);
        font-size: rel-val(120, 16);
    }

    &__title-short {
        margin-bottom: 0.1em;
        opacity: 0;
        animation: primary-opacity 1s linear 0s forwards;
    }

    &__title-anim {
        margin-bottom: -0.1em;
        height: 1.05em;
        overflow: hidden;
        color: #f9ee8e;
    }

    &__title-anim-inner {
        display: flex;
        flex-direction: column;
        opacity: 0;
        animation: primary-title-anim 9s infinite 0.5s;
    }

    &__bottom {
        display: flex;
        align-items: flex-end;
        flex-wrap: wrap;
        justify-content: space-between;
        color: rgba(255, 255, 255, 0.8);
    }

    &__text {
        font-family: "Poppins-Regular", Arial, sans-serif;
        line-height: 1.4;
        max-width: 570px;
        opacity: 0;
        white-space: nowrap;
        animation: primary-opacity 1s linear 0.5s forwards;
        letter-spacing: rel-val(0.4, 28);
        margin-right: rel-val(65, 28);
        margin-bottom: rel-val(20, 28);
        font-size: rel-val(28, 16);
    }

    &__pointer {
        display: flex;
        align-items: center;
        margin-bottom: rel-val(20, 16);
        opacity: 0;
        transform: rotate(90deg);
        animation: primary-opacity 1s linear 0s forwards, primary-pointer-rotate 0.5s linear 0s forwards;
        .icon {
            transform: rotate(135deg);
            margin-right: 12px;
            width: 14px;
        }
    }

    &__pointer-text {
        font-family: "Oswald-Medium", Arial, sans-serif;
        line-height: 2.58;
        letter-spacing: 0.4px;
        text-transform: uppercase;
        font-size: 18px;
        white-space: nowrap;
    }
}

@keyframes primary-opacity {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes primary-title-anim {
    0% {
        opacity: 0;
        margin-top: 0.95em;
    }
    12% {
        opacity: 1;
        margin-top: -0.1em;
    }
    17% {
        margin-top: -0.1em;
    }
    38% {
        margin-top: -0.1em;
    }
    43% {
        margin-top: -1.15em;
    }
    69% {
        margin-top: -1.15em;
    }
    74% {
        margin-top: -2.2em;
    }
    100% {
        opacity: 1;
        margin-top: -2.2em;
    }
}

@keyframes primary-pointer-rotate {
    0% {
        transform: rotate(90deg);
    }
    100% {
        transform: rotate(0);
    }
}
