.footer {
    @include fluidFontSize(16, 0, 420, 30);
    background: $color-primary;
    position: relative;
    color: #fff;

    &__background {
        position: absolute;
        bottom: 0;
        left: 0;
        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            background: linear-gradient(180deg, #1b75bc 0%, rgba(27, 117, 188, 0.2) 55.94%), linear-gradient(270deg, #1b75bc 0%, rgba(27, 117, 188, 0.2) 52.58%);
        }
        img {
            width: 100%;
        }
    }

    &__container {
        max-width: 1224px;
        position: relative;
    }

    &__content {
        padding-top: rel-val(100, 16);
        font-family: "Poppins-Regular", Arial, sans-serif;
        max-width: 1180px;
        margin-bottom: rel-val(13, 16);
    }

    &__top-wrapper {
        margin-left: -25px;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }

    &__contacts {
        flex-grow: 5.96;
        align-items: flex-end;
        justify-content: space-between;
        @include max(700) {
            display: flex;
        }
    }

    &__contacts-content {
        margin-left: 25px;
        flex-shrink: 0;
    }

    &__logo {
        margin-bottom: 40px;
        display: block;
        width: 198px;
    }

    &__list {
        margin-bottom: rel-val(59, 16);
        max-width: 245px;
    }

    &__list-item {
        margin-bottom: 12px;
        line-height: 1.5;
        font-size: 16px;
    }

    &__map {
        display: none;
        @include min(470) {
            margin-left: 25px;
            margin-bottom: 59px;
            border: 4px solid #fff;
            min-width: 100px;
            max-width: 276px;
            max-height: 152px;
            flex-grow: 1;
            display: flex;
        }
    }

    &__map-inner {
        border: 0;
        width: 100%;
    }

    &__links-group {
        display: flex;
        flex-grow: 10;
        justify-content: space-between;
        margin-bottom: rel-val(59, 16);
    }

    &__links {
        margin-left: 25px;
        max-width: 262px;
        flex-grow: 1;
    }

    &__links-title {
        margin-bottom: 20px;
        font-size: 24px;
        line-height: 1.38;
    }

    &__link {
        height: rel-val(56, 16);
        display: flex;
        align-items: center;
        border-bottom: 1px solid rgba(255, 255, 255, 0.2);
        & > .icon {
            margin-left: 10px;
            margin-right: 2px;
            width: 11px;
            flex-shrink: 0;
            transition: transform 0.3s;
        }
        &:hover > .icon {
            transform: rotate(-135deg);
        }
    }

    &__link-text {
        flex-grow: 1;
        line-height: 1.5;
    }

    &__link-icon .icon {
        width: 25px;
        margin-right: 11px;
    }

    &__certificates {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        margin-left: rel-val(-32, 16);
        margin-bottom: rel-val(32, 16);
    }

    &__certificates-group {
        display: flex;
    }

    &__certificate {
        margin-left: rel-val(32, 16);
        margin-bottom: rel-val(32, 16);
        position: relative;
        pointer-events: none;
        &[title]::before {
            content: attr(title);
            transform: scaleY(0.7);
            line-height: 0;
            bottom: 41%;
            position: absolute;
            left: 0;
            right: 0;
            text-align: center;
            color: #ffecaa;
        }
        img {
            max-width: rel-val(110.06, 16);
            max-height: rel-val(120, 16);
        }
    }

    &__footer {
        font-family: "Proxima-Regular", Arial, sans-serif;
        font-size: rel-val(15.6, 16);
        display: flex;
        padding-bottom: rel-val(50, 15.6);
        line-height: 1.5;
        flex-wrap: wrap;
        flex-direction: row-reverse;
        justify-content: flex-end;
        margin-left: -17px;
        &::before {
            content: "";
            display: block;
            width: 100%;
            border-top: 1px solid rgba(255, 255, 255, 0.2);
            padding-bottom: rel-val(32, 15.6);
            margin-left: 23px;
        }
    }

    &__label {
        flex-grow: 1;
        margin-left: 23px;
    }

    &__info {
        margin-left: 23px;
        text-decoration: underline;
    }
}
