.news {
    overflow: hidden;
    color: $color-dark;
    padding: rel-val(150.92, 16) 0 rel-val(154.97, 16);
    @include fluidFontSize(16, 0, 526, 30);

    &__container {
        max-width: 1224px;
        position: relative;
    }

    &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: rel-val(43, 16);
        margin-left: -100px;
        flex-wrap: wrap;
        position: relative;
        z-index: 1;
    }

    &__ellipse {
        z-index: -1;
        position: absolute;
        width: 40vw;
        height: 40vw;
        max-width: 284px;
        max-height: 284px;
        left: 82.8%;
        transform: translate(-50%, 16.3%);
        background: $color-primary;
        border-radius: 50%;
        filter: blur(rel-val(200, 16));
    }

    &__title {
        font-size: rel-val(90, 16);
        line-height: 1.111em;
        letter-spacing: 0.004em;
        margin-left: 100px;
        margin-bottom: rel-val(20, 90);
    }

    &__link {
        font-family: "Oswald-Medium", Arial, sans-serif;
        font-size: 14px;
        margin-bottom: 20px;
        line-height: 20px;
        letter-spacing: 0.8px;
        text-transform: uppercase;
        color: $color-primary;
        margin-top: 32px;
        display: flex;
        margin-left: 100px;
        .icon {
            width: rel-val(14, 14);
            margin-left: rel-val(9, 14);
            margin-right: rel-val(3, 14);
            transition: transform 0.3s;
        }
        &:hover .icon {
            transform: rotate(-135deg);
        }
    }

    &__swiper {
        width: 180%;
        @include min(587) {
            width: calc(50vw + 50% + 83px);
        }
    }

    &__slide-img {
        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 10px;
        }
    }

    &__slide-description {
        font-size: 28px;
        line-height: 1.357em;
        letter-spacing: 0.007em;
        @include fluidFontSize(33, 0, 587, 30);
        @include fluidFontSize(28, 587, 902, 70);
        @include fluidFontSize(28, 902, 1600, -240);
        margin-top: rel-val(28, 28);
        font-family: "Poppins-Regular", Arial, sans-serif;
        display: flex;
        flex-direction: row-reverse;
        justify-content: flex-end;
        align-items: flex-end;
        transition: color 0.3s;
        .news__slide:hover & {
            color: $color-primary;
        }
        .icon {
            color: $color-primary;
            width: rel-val(36, 28);
            margin: 0 rel-val(27, 28) rel-val(9, 28) rel-val(5, 28);
            transition: transform 0.3s;
            .news__slide:hover & {
                transform: rotate(45deg);
            }
        }
    }

    &__swiper-buttons {
        @include max(587) {
            display: none;
        }
    }
}
