.career {
    position: relative;
    font-size: 16px;
    @include fluidFontSize(16, 0, 510, 30);
    @include fluidFontSize(16, 730, 1020, 30);
    color: #fff;
    padding-top: 92px;
    min-height: 100vh;

    &__container {
        max-width: 1224px;
    }

    &__wrapper {
        @include max(730) {
            flex-direction: column-reverse;
            align-items: center;
        }
        padding-top: rel-val(100, 16);
        max-width: 1092px;
        display: flex;
        justify-content: space-between;
    }

    &__info {
        @include max(730) {
            margin: 4.5em 0;
            font-size: 1.1em;
        }
        flex-grow: 1;
        font-family: "Poppins-Regular", Arial, sans-serif;
        max-width: rel-val(480, 16);
        margin: rel-val(120, 16) rel-val(50, 16) rel-val(50, 16) 0;
    }

    &__label {
        display: inline-flex;
        align-items: center;
        background: #fff;
        color: $color-dark;
        font-size: 10px;
        line-height: 2.4;
        letter-spacing: 0.2px;
        text-transform: uppercase;
        padding: 2px 16px;
        border-radius: 32px;
        margin-bottom: 11px;
    }

    &__label-img {
        height: 12px;
        margin-right: 8px;
    }

    &__label-text {
        text-shadow: 0 0 #000;
    }

    &__list-link {
        display: flex;
        justify-content: space-between;
        transition-property: padding, background, color;
        transition-duration: 0.3s;
        margin-bottom: -1px;
        padding-right: rel-val(20, 16);
        &:hover {
            color: $color-dark;
            background: #fff;
            padding-right: 0;
            padding-left: rel-val(20, 16);
        }
        .icon {
            color: $color-primary;
            margin: 0 rel-val(31, 16) 0 rel-val(15, 16);
            width: rel-val(36, 16);
        }
    }

    &__list-link-text {
        border-bottom: dashed 1px #fff;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: rel-val(13, 16) 0;
        min-width: rel-val(332, 16);
    }

    &__list-link-title {
        line-height: 1.6;
        font-size: rel-val(20, 16);
        letter-spacing: rel-val(0.2, 20);
    }

    &__list-link-subtitle {
        font-size: rel-val(14, 16);
        letter-spacing: rel-val(0.2, 14);
        line-height: rel-val(36, 14);
    }

    &__text {
        width: rel-val(480, 16);
    }

    &__title {
        color: rgb(12, 39, 66);
        font-style: normal;
        line-height: 1.11;
        font-size: rel-val(90, 16);
        margin-bottom: rel-val(40, 90);
        letter-spacing: rel-val(0.4, 90);
        transform-origin: 50% 100%;
        @include min(730) {
            transform-origin: 116% -80%;
        }
    }

    &__description {
        font-family: "Poppins-Regular", Arial, sans-serif;
        font-size: rel-val(20, 16);
        line-height: rel-val(32, 20);
        letter-spacing: rel-val(0.2, 20);
        margin-bottom: rel-val(42, 20);
    }

    &__link {
        font-family: "Oswald-Medium", Arial, sans-serif;
        font-size: 14px;
        letter-spacing: 0.8px;
        text-transform: uppercase;
        display: flex;
        align-items: center;
        .icon {
            width: 14px;
            margin-left: 8px;
            transition: transform 0.3s;
        }
        &:hover .icon {
            transform: rotate(-135deg);
        }
    }
}
