@use 'sass:math';

%svg-common {
	background: url("../images/sprites/main.svg") no-repeat;
}

.svg-image-arrow {
	@extend %svg-common;
	background-position: 88.31168831168831% 13.710247349823321%;
	background-size: math.div(738, 45)*100% auto;
	width: 45px;

}
@mixin svg-pos-image-arrow {
	background-position: 88.31168831168831% 13.710247349823321%;
}
@mixin svg-image-arrow-hover {
	.svg-image-arrow {
		@include svg-pos-image-arrow-hover
	}
}

.svg-image-arrow:before {
	content:"";
	display:block;
	font-size:0;
	line-height:0;
	padding-top: math.div(45, 45)*100%;
}

.svg-image-arrow-dims {
	width: 45px;
	height: 45px;
}

