.swiper-buttons {
    display: flex;
    justify-content: space-between;
    width: 250px;
    padding: 15px;
    margin-left: rel-val(29, 16);
    margin-top: rel-val(63, 16);
    @include max(587) {
        margin-left: auto;
        margin-right: auto;
    }
}

.swiper-button-prev {
    transform: rotate(-135deg);
}

.swiper-button-next {
    transform: rotate(45deg);
}

.swiper-button-prev,
.swiper-button-next {
    position: static;
    margin: 0;
    width: 70.71px;
    height: 70.71px;
    flex-shrink: 0;
    background: rgba(27, 117, 188, 0.1);
    border: 2px solid $color-primary;
    transition: background 0.3s;
    .icon {
        width: 20px;
        color: $color-primary;
        transition: color 0.3s;
    }
    &::after {
        display: none;
    }
    &:hover {
        background: $color-primary;
        .icon {
            color: #eef2f5;
        }
    }
}
