.about {
    font-size: 16px;
    @include fluidFontSize(16, 0, 992, 30);
    position: relative;
    display: flex;
    align-items: center;
    min-height: 100vh;
    padding-top: 46px;

    &__container {
        max-width: 1224px;
        display: flex;
    }

    &__wrapper {
        display: flex;
        margin-left: calc(50vw - 50% + 100%);
        margin-left: 0%;
        transform: translateX(-100%);
        transform: translateX(0%);
    }

    &__item {
        display: flex;
        align-items: flex-start;
        transform-origin: 50% 25%;
        @include min(992) {
            align-items: flex-end;
        }
        &--first {
            min-width: 100vw;
            margin-right: 50px;
        }
        &--last {
            min-width: 100vw;
            justify-content: center;
        }
    }

    &__text {
        color: $color-dark;
        .about__item--first & {
            margin-right: rel-val(50, 16);
            width: rel-val(390, 16);
            @include min(992) {
                margin-bottom: 123px;
                width: 480px;
                margin-right: calc(30.25vw - 250px);
            }
            @include min(1600) {
                margin-right: 234px;
            }
        }
        .about__item--last & {
            margin-left: rel-val(70, 16);
            width: rel-val(590, 16);
            margin-right: 30px;
            @include min(992) {
                margin-bottom: 123px;
                margin-left: 71px;
                width: 590px;
            }
        }
    }

    &__title {
        font-size: rel-val(90, 16);
        line-height: 1.11;
        letter-spacing: rel-val(0.4, 90);
        margin-bottom: rel-val(50, 90);
        .about__item--first & {
            @include min(992) {
                margin: 0 0 114px 100px;
            }
        }
        .about__item--last & {
            margin-right: rel-val(70, 90);
            @include min(992) {
                margin: 0 120px 74px 0;
            }
        }
    }

    &__title-contrast {
        color: $color-primary;
    }

    &__description {
        font-family: "Poppins-Regular", Arial, sans-serif;
        font-size: rel-val(36, 16);
        line-height: 1.36;
        letter-spacing: rel-val(0.2, 28);
        @include min(992) {
            font-size: 28px;
        }
        .about__item--first & {
            @include min(992) {
                margin-right: 90px;
            }
        }
        .about__item--last & {
            margin-left: rel-val(150, 36);
            @include min(992) {
                margin-left: 102px;
            }
        }
    }

    &__content {
        position: relative;
        @include min(992) {
            margin-bottom: 123px;
        }
    }

    &__img {
        position: relative;
        overflow: hidden;
        border-radius: 10px;
        img {
            .about__item--first & {
                @include max(992) {
                    width: calc(77.4vw - 30px);
                }
            }
            .about__item--last & {
                @include max(992) {
                    width: calc(71vw - 30px);
                }
            }
        }
        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background: linear-gradient(111.01deg, rgba(12, 39, 66, 0.5) 0%, rgba(12, 39, 66, 0) 94.51%);
        }
    }

    &__link {
        max-width: 174px;
        max-height: 174px;
        width: rel-val(280, 16);
        height: rel-val(280, 16);
        font-family: "Oswald-Medium", Arial, sans-serif;
        background: red;
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #fff;
        border: 2px solid #fff;
        background: $color-primary;
        box-shadow: 4px 4px 40px rgba(27, 117, 188, 0.4);
        transition-property: background, border-color, color;
        transition-duration: 0.3s;
        &:hover {
            border-color: $color-primary;
            background: #fff;
            color: $color-primary;
        }
        .about__item--first & {
            bottom: 0;
            left: 27%;
            transform: translate(-50%, 70%) rotate(45deg);
            @include min(992) {
                left: 0;
                transform: translate(-50%, 50%) rotate(45deg);
            }
        }
        .about__item--last & {
            transform: translate(50%, 50%) rotate(45deg);
            right: 0;
            bottom: 33%;
            @include min(992) {
                bottom: 0;
            }
        }
    }

    &__link-inner {
        @include fluidFontSize(14, 0, 401, 30);
        width: rel-val(82, 14);
        white-space: nowrap;
        font-size: 14px;
        line-height: 1.429;
        text-align: justify;
        letter-spacing: 0.8px;
        text-transform: uppercase;
        transform: rotate(-45deg);
        margin-right: 2px;
        .icon {
            margin: 6px auto -17px;
            display: block;
            width: 14px;
            color: #fff;
            transition-property: transform, color;
            transition-duration: 0.3s;
            @include min(992) {
                margin: 8px auto 5px;
            }
            .about__link:hover & {
                color: $color-primary;
                transform: rotate(-45deg);
            }
        }
    }
}
