.testimonials {
    background: #eef2f5;
    padding-top: rel-val(63, 16);
    @include fluidFontSize(16, 700, 1070, 30);
    @include fluidFontSize(16, 0, 644, 30);

    &__container {
        max-width: 1228px;
    }

    &__content {
        max-width: 1399px;
        @include min(700) {
            width: calc(50vw + 50%);
        }
    }

    &__content-wrapper {
        display: flex;
        align-items: flex-end;
        flex-direction: column-reverse;
        @include min(700) {
            flex-direction: row;
            margin-left: rel-val(-40, 16);
        }
    }

    &__swiper {
        flex-grow: 1;
        max-width: 614px;
        width: 100%;
        height: 100%;
        position: relative;
        margin-bottom: rel-val(40, 16);
        @include min(700) {
            margin-left: rel-val(40, 16);
        }
        & > .icon {
            position: absolute;
            width: rel-val(72, 16);
        }
    }

    &__slide-text {
        font-family: "Poppins-Regular", Arial, sans-serif;
        font-style: italic;
        font-size: rel-val(32, 16);
        margin: rel-val(32, 32) rel-val(10, 32) 0 rel-val(31, 32);
        line-height: 1.375;
        letter-spacing: rel-val(0.2, 32);
        color: $color-dark;
    }

    &__reviews {
        position: relative;
        color: #fff;
        width: rel-val(536, 16);
        flex-shrink: 0;
        margin-left: rel-val(40, 16);
        margin-bottom: rel-val(40, 16);
    }

    &__symbol {
        width: rel-val(536, 16);
        position: absolute;
        bottom: -1px;
        color: rgb(2, 173, 35, 1);
        transition: transform 0.1s;
        .icon {
            width: 100%;
        }
    }

    &__reviews-content {
        display: flex;
        flex-direction: column;
        padding: 0 rel-val(36, 16) rel-val(39, 16);
        width: rel-val(536, 16);
    }

    &__stars-rating {
        margin: 0 auto rel-val(17, 16);
        position: relative;
        .icon {
            width: rel-val(24, 16);
            margin: 0 rel-val(4.7, 16);
        }
    }

    &__rating {
        position: relative;
        font-family: "Nexa-Bold", Arial, sans-serif;
        font-size: rel-val(80, 16);
        line-height: 1.513;
        letter-spacing: rel-val(0.4, 80);
        margin-bottom: rel-val(29, 80);
        margin-right: rel-val(30, 80);
        text-align: center;
    }

    &__text {
        position: relative;
        font-family: "Nexa-Bold", Arial, sans-serif;
        font-size: rel-val(24, 16);
        line-height: 1.5;
        letter-spacing: rel-val(0.4, 24);
        margin-bottom: rel-val(15, 24);
    }

    &__reviews-footer {
        position: relative;
        display: flex;
        align-items: flex-end;
        & > .icon {
            width: rel-val(81, 16);
            margin-left: rel-val(-8, 16);
            margin-bottom: rel-val(15, 16);
        }
    }

    &__link {
        display: block;
        margin-left: rel-val(58, 16);
        width: rel-val(90, 16);
        height: rel-val(90, 16);
        box-shadow: rel-val(4, 16) rel-val(4, 16) rel-val(40, 16) rgba(27, 117, 188, 0.4);
        background: #02ad23;
        border: 2px solid #fff;
        transform: rotate(45deg);
        display: flex;
        justify-content: center;
        align-items: center;
        transition-property: color, border-color, background;
        transition-duration: 0.3s;
        .icon {
            transition: transform 0.3s;
            width: rel-val(14, 16);
        }
        &:hover {
            border-color: rgb(2, 173, 35);
            background: #fff;
            color: rgb(2, 173, 35);
            & .icon {
                transform: rotate(-45deg);
            }
        }
    }

    &__link-inner {
        font-family: "Poppins-Regular", Arial, sans-serif;
        text-transform: uppercase;
        font-size: rel-val(14, 16);
        transform: rotate(-45deg);
        align-items: center;
        display: flex;
        flex-direction: column;
        & .icon {
            margin-top: rel-val(10, 14);
        }
    }

    &__title {
        font-family: "Nexa-Bold", Arial, sans-serif;
        text-align: center;
        font-size: 198px;
        line-height: 1;
        letter-spacing: rel-val(1.6, 198);
        color: $color-dark;
        opacity: 0.05;
        padding-bottom: rel-val(188, 198);
        @include fluidFontSize(198, 0, 1300, 30);
    }
}
