.header {
    z-index: 10;
    color: #fff;
    height: 92px;
    font-size: 18px;
    line-height: 1.333;
    position: fixed;
    width: 100%;

    &--shifted,
    .opened & {
        transition: background 0.3s;
        background: #fff;
        color: $color-dark;
        box-shadow: 0 0 0 #000;
    }
    .opened & {
        height: 100vh;
        font-size: 25px;
    }

    &__container {
        height: 100%;
        max-width: 1350px;
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        flex-wrap: wrap;
        .header--shifted &,
        .opened & {
            align-items: center;
        }
    }

    &__logos {
        width: 174px;
        transition: transform 0.3s;
        transform-origin: 0 50%;
        .header--shifted &,
        .opened & {
            transform: scale(0.9);
        }
    }

    &__logo-primary {
        display: block;
        .header--shifted &,
        .opened & {
            display: none;
        }
    }

    &__logo-default {
        display: none;
        .header--shifted &,
        .opened & {
            display: block;
        }
    }

    &__content {
        overflow: auto;
        display: flex;
        height: 100%;
        align-items: inherit;
        .opened & {
            order: 1;
            display: block;
            width: 100%;
        }
        @media (max-width: 899px) {
            display: none;
        }
    }

    &__nav {
        height: 100%;
        .opened & {
            height: auto;
        }
    }

    &__nav-list {
        height: 100%;
        display: flex;
        .opened & {
            align-items: center;
            flex-direction: column;
            margin-bottom: 25px;
        }
    }

    &__nav-item {
        height: 100%;
    }

    &__nav-link {
        border-radius: 0 0 4px 4px;
        height: 100%;
        display: flex;
        align-items: flex-end;
        padding: 0 17px 19px 17px;
        transition-duration: 0.3s;
        transition-property: background, color;
        .header--shifted &,
        .opened & {
            align-items: center;
            padding-bottom: 0;
            border-radius: 0;
        }
        .opened & {
            padding: 16px 25px;
        }
        &:hover {
            background: $color-primary;
            color: #fff;
        }
        &--selected {
            background: $color-primary;
            color: #fff;
        }
    }

    &__btn {
        display: flex;
        align-items: center;
        padding: 14px 22.5px;
        background: #fff;
        border-radius: 4px;
        color: $color-primary;
        margin: 0 0 3px 16px;
        transition-duration: 0.3s;
        transition-property: background, border-color, color;
        border-style: solid;
        border-width: 2px;
        border-color: #fff;
        &:hover {
            border-color: $color-primary;
            background: $color-primary;
            color: #fff;
        }
        .header--shifted &,
        .opened & {
            border-color: $color-primary;
            background: $color-primary;
            color: #fff;
            &:hover {
                background: #fff;
                color: $color-primary;
            }
        }
        .opened & {
            transform: translateX(-50%);
            margin: 0 0 0 50%;
            display: inline-block;
        }
    }

    &__burger {
        cursor: pointer;
        align-self: center;
        background: none;
        border: none;
        position: relative;
        width: 40px;
        min-width: 25px;
        height: 40px;
        margin: 26px 0;
        @media (min-width: 900px) {
            display: none;
            .opened & {
                display: block;
            }
        }
    }

    &__burger-line {
        position: absolute;
        height: 4px;
        width: 100%;
        background: #fff;
        display: block;
        left: 0;
        transition: 0.3s ease-in-out;
        &:nth-child(1) {
            top: 5px;
        }
        &:nth-child(2) {
            top: calc(50% - 2px);
        }
        &:nth-child(3) {
            top: calc(100% - 9px);
        }
        .opened & {
            &:nth-child(1) {
                top: calc(50% - 2px);
                transform: rotate(-45deg);
            }
            &:nth-child(2) {
                width: 0%;
                left: 50%;
                transform: rotate(360deg);
            }
            &:nth-child(3) {
                top: calc(50% - 2px);
                transform: rotate(-135deg);
            }
        }
        .header--shifted &,
        .opened & {
            background: $color-dark;
        }
    }
}
